body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

body {
  font-family: 'Raleway', sans-serif;
}

.head, .bod, .foot {
  -moz-animation: fadein 4s; /* Firefox */
  -webkit-animation: fadein 4s; /* Safari and Chrome */
  -o-animation: fadein 4s; /* Opera */
}

.App-logo {
  border-radius: 50%;
  height: 40vmin;
  pointer-events: none;
  padding: 10px;
  border: double 1px rgb(192, 187, 187);
  margin-top: 5vh;
}

.App-header {
  min-height: 100vh;
  min-width: 100vw;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: rgb(214, 206, 206);
  background-image: url(/static/media/bg.ad11bb37.jpg);
  background-size: cover;
}

.App-projects {
  min-height: 100vh;
  min-width: 100vw;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: rgb(214, 206, 206);
  text-align: center;
}

.App-footer {
  min-height: 20vh;
  min-width: 100vw;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: rgb(214, 206, 206);
  background-image: url(/static/media/bg.ad11bb37.jpg);
  background-size: cover;
}

.aboutBt {
  border: solid;
  padding: 1vh;
}

.aboutBt:hover {
  color: #cdd9f5;
  -webkit-transform: scale(1.025);
          transform: scale(1.025);
}

.aboutMe {
  font-size: 15px;
  position: relative;
  width: 360px;
  margin: 3vh 10vw
}

.contact {
  position: relative;
  width: 360px;
  margin: 3vh 10vw
}

a {
  color: inherit;
  text-decoration: none;
}

.social {
  margin: 1vh;
  color: #cdd9f5;
}

.social:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  color: #cdd9f5;
  transition: border-bottom-color 0.2s ease-in-out, color 0.2s ease-in-out;
}

.App-link {
  color: #61dafb;
}

.btn, .btn-outline-primary, .btn-sm {
  color:#cdd9f5;
  outline: none;
  box-shadow: none!important;
}

.btn:hover, .btn:active {
  background-color:#cdd9f5!important;
  color:rgb(122, 118, 118)!important;
  border-color: #cdd9f5!important;
}

.rotateDown{
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg);
  transition: .5s;
}

.rotateBack{
  -webkit-transform: rotate(0deg);
          transform: rotate(0deg);
  transition: .5s;
}



.name {
  transition: 0.5s;
  color: rgb(221, 80, 153);
  opacity: 0.8;

}

.nameHover {
  transition: 0.5s;
  color: #E31B6D;
  
}

.projectsTitle {
  border-radius: 20%;
  font-size: 70px;
  text-transform: uppercase;
  letter-spacing: 6;
  text-decoration: underline;
  padding: 10px;
  margin: 20px;
}

.clickableImages {
  height: 300px;
  width: 210px;
  border-radius: 10px;
  outline: none;
}

.projbtn {
  border: 0;
  padding: 0;
  outline: none!important;
  -webkit-user-select: none!important;
     -moz-user-select: none!important;
      -ms-user-select: none!important;
          user-select: none!important;
}

@-webkit-keyframes fadein {
  from {
      opacity:0;
  }
  to {
      opacity:1;
  }
}

@keyframes fadein {
  from {
      opacity:0;
  }
  to {
      opacity:1;
  }
}

@media only screen and (max-width:800px) {
  /* For tablets: */
  .App {
    width: 80%;
    padding: 0;
  }
}
@media only screen and (max-width:500px) {
  /* For mobile phones: */
  .App {
    width: 100%;
  }
}

